import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectFriends} from '../../store/reducers';
import {frendsActions} from '../../store/actions';

import {Panel,Group} from '@vkontakte/vkui';
import HeaderPanel from '../../components/HeaderPanel';

import { LineChart, Line, PieChart, Pie, Sector, Cell, Legend } from 'recharts';

import db from './db_friends.json';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#FF80333', '#FA8542'];
const style = {
	top: 0,
	left: '3px',
	lineHeight: '24px'
};

const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
	const RADIAN = Math.PI / 180;
	const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
	const x  = cx + radius * Math.cos(-midAngle * RADIAN);
	const y = cy  + radius * Math.sin(-midAngle * RADIAN);

	return (<text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central" />);
};

class City extends Component {
	constructor(props) {
		super(props);
	}

	componentWillMount() {
		const {dispatch} = this.props;
		const {fetchedAllFriends} = frendsActions;
		dispatch(fetchedAllFriends());
	}

	render () {
		const {id, go, users} = this.props;

		const friends = {};
		// db.response.items
		db.response.items.map((friend) => {
			if (friend.city){
				if (friends[friend.city.id]) {
					friends[friend.city.id].value += 1;
					friends[friend.city.id].name = friend.city.title;
				} else {
					friends[friend.city.id] = {
						id: friend.city.id,
						name: `${friend.city.title}`,
						value: 1
					};
				}
			}
		});
		const friendsSort = {};
		const friendsVslues = Object.values(friends);
		friendsVslues.map(friend => {
			if (friend.value < 5) {
				if (!friendsSort['others']){
					friendsSort['others'] = {
						value: 0,
						items: [],
						name: ''
					};
				}
				friendsSort['others'].items.push(friend);
				friendsSort['others'].value += 1;
				friendsSort['others'].name = `others - ${friendsSort['others'].value}`;
			} else {
				if (!friendsSort[friend.id]) {
					friendsSort[friend.id] = {
						value: 0,
						items: [],
						name: ''
					};
				}
				friendsSort[friend.id].items.push(friend);
				friendsSort[friend.id].value += friend.value;
				friendsSort[friend.id].name = `${friend.name} - ${friendsSort[friend.id].value}`;
			}
		});
		debugger;

		const data = Object.values(friendsSort).sort((a,b) => b.value - a.value);

		// const data = users;

		return (
			<Panel id={id}>
				<HeaderPanel title='Города' handelClick={go} dataTo='home' />
				<Group>
					<PieChart width={320} height={300} onMouseEnter={this.onPieEnter}>
						<Pie
							dataKey='value'
							data={data} 
							cx="70%" 
							cy="30%" 
							labelLine={false}
							label={renderCustomizedLabel}
							outerRadius={80} 
							fill="#8884d8"
						>
							{data.map((entry, index) => <Cell key={index} fill={COLORS[index % COLORS.length]}/>)}
						</Pie>
						<Legend 
							iconSize={10}
							iconType='circle'
							width={320}
							height={5}
							layout='vertical'
							verticalAlign='middle'
							wrapperStyle={style}/>
					</PieChart>
				</Group>
			</Panel>
		);
	}
}

City.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	users: selectFriends.getSortFriendsFromCity(state)
});
export default connect(mapStateToProps, null)(City);